<template>
  <div class="udesly-completely-wrapper">
    <div udesly-completely v-bind:class="{'valid': valid}"></div>
    <div v-if="enabled" class="udesly-button" @click="$emit('and')" :class="{'selected': combination == 'and'}">AND</div>
    <div v-if="enabled" class="udesly-button" @click="$emit('or')" :class="{'selected': combination == 'or'}">OR</div>
    <i @click="$emit('remove')" v-if="!first" class="fas fa-trash" style="cursor: pointer;"></i>
  </div>
</template>

<script>
import completely from "../lib/completely";
import { flatUpStructure } from "../lib/utils";

export default {
  name: "udesly-autocomplete",
  props: ["predicate", "subjects", "value", "combination", "first", "enabled"],
  data: () => ({
    valid: false
  }),
  mounted: function() {
    const pv = completely(this.$el.querySelector("[udesly-completely]"), {
      fontFamily: "Roboto"
    });

    const { predicate, subjects } = this;

    const combi = flatUpStructure(predicate, subjects);
    const t = this;
    pv.onChange = function(text) {
      t.valid = false;
      t.$emit("validity-change", false);
      t.$emit("input", []);
      if (text.includes("always")) {
        t.valid = true;
         t.$emit("validity-change", true);
        t.$emit("input", ["always"]);
        return;
      }
      // search the matching combination.
      console.log(text);
      for (var i = 0; i < combi.length; i++) {
        if (text.includes(combi[i].id)) {
          console.log(true, combi[i].id)
        }
        if (text.includes(combi[i].id)) {
          pv.startFrom = combi[i].id.length;
          pv.options = combi[i].options;
          if (combi[i].options[0] == ".") {
            //t.value = combi[i].value;
            console.log(combi[i].value);
            console.log(i);
            t.$emit("input", combi[i].value);
            t.valid = true;
             t.$emit("validity-change", true);
          } else {
            t.valid = false;
             t.$emit("validity-change", false);
          }
          if (combi[i].value) {
            const cleanedValue =
              predicate +
              " " +
              combi[i].value.join(" ").replace(/_/g, " ") +
              " ";
              console.log(cleanedValue);
            const lastPath = text.replace(cleanedValue, "");
            console.log(lastPath);
            console.log(combi[i].value)
            if (
              combi[i].value.length == 2 &&
              (!lastPath.includes(predicate) &&
                lastPath != "" &&
                !lastPath.includes("{"))
            ) {
              t.valid = true;
               t.$emit("validity-change", true);
              //t.value = [...combi[i].value, lastPath];
              t.$emit("input", [...combi[i].value, lastPath]);
            }
          } else {
            t.valid = false;
             t.$emit("validity-change", false);
          }

          pv.repaint();
          return;
        }
      }
    };
    pv.options = combi[combi.length - 1].options;
    setTimeout(function() {
      pv.input.onfocus = function() {
        t.$el.classList.add("focused");
      };
      pv.input.onfocusout = function() {
        t.$el.classList.remove("focused");
      };
      if (t.value.length) {
        pv.setText(t.predicate + " " + t.value.join(" "))
      }
      pv.repaint();
      pv.input.dispatchEvent(new Event('change'));
    }, 0);
  }
};
</script>

<style lang="scss">
@import url("https://fonts.googleapis.com/css?family=Roboto");
.udesly-completely-wrapper {
  display: flex;
  align-items: center;
  margin-top: 8px;
  margin-bottom: 8px;
}
[udesly-completely] {
  width: 90%;
  margin-right: 4px;
}
[udesly-completely] * {
  font-family: Roboto !important;
}
[udesly-completely].valid > div {
  box-shadow: 0 0 1px 1px #00cb5d;
}
.focused [udesly-completely] > div {
  overflow: unset;
  z-index: 9999999999 !important;
}
[udesly-completely] > div {
  padding: 8px !important;
  border-radius: 4px;
  box-shadow: 0 0 1px 1px red;
  overflow: hidden;

  input:first-of-type {
    margin-top: 2px !important;
    margin-left: 2px !important;
    padding: 6px !important;
    width: 90% !important;
  }
  input {
    outline: none !important;
    box-shadow: none;
    &:focus {
      outline: none !important;
      box-shadow: none;
    }
  }
}
.udesly-dropdown-autocomplete {
  border: 1px solid #dadada;
  border-radius: 4px;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1);
  background-color: white;
  & > div {
    border: 1px solid transparent;
    padding: 6px 12px;
    cursor: pointer;
  }
}
.focused .udesly-dropdown-autocomplete {
  z-index: 9999999999 !important;
}

.udesly-button {
  &.selected {
    background-color: #00cb5d;
  }
  margin-left: 4px;
  margin-right: 4px;
  cursor: pointer;
  position: relative;
  display: inline-block;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: none;
  border-radius: 4px;
  padding: 0 16px;
  min-width: 64px;
  height: 36px;
  vertical-align: middle;
  text-align: center;
  text-overflow: ellipsis;
  text-transform: uppercase;
  color: rgb(var(--pure-material-onprimary-rgb, 255, 255, 255));
  background-color: rgb(var(--pure-material-primary-rgb, 33, 150, 243));
  -webkit-box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2),
    0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
  font-family: var(
    --pure-material-font,
    "Roboto",
    "Segoe UI",
    BlinkMacSystemFont,
    system-ui,
    -apple-system
  );
  font-size: 14px;
  font-weight: 500;
  line-height: 36px;
  overflow: hidden;
  outline: none;
  cursor: pointer;
  -webkit-transition: -webkit-box-shadow 0.2s;
  transition: -webkit-box-shadow 0.2s;
  transition: box-shadow 0.2s;
  transition: box-shadow 0.2s, -webkit-box-shadow 0.2s;
  &:focus {
    -webkit-box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2),
      0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12);
    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2),
      0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12);
  }
  &:active {
    -webkit-box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2),
      0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
    box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2),
      0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
  }
}
</style>
