<template>
  <div id="udesly-rules-configuration">
    <AutoComplete
      v-for="(value, index) in values"
      :enabled="enabledAdd"
      :combination="combinations[index] || ''"
      :predicate="predicate"
      :first="index !== values.length -1 || index == 0"
      @remove="handleRemove(index)"
      :subjects="subjects"
      v-model="values[index]"
      @and="handleAnd(index)"
      @or="handleOr(index)"
      @validity-change="handleValidityChange(index, $event)"
      :key="index"
    />
    <div class="udesly-actions" style="text-align: right; margin-top: 150px">
      <button class="udesly-button" @click.prevent="save" :disabled="!isValid()">{{ buttonLabel }}</button>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import AutoComplete from "./components/AutoComplete";
import { ajaxCall } from "./lib/utils";

const {
  nonce,
  ajaxurl,
  post_id,
  predicate,
  subjects,
  saveLabels,
  data
} = window.udeslyRules;

console.log(data);

let values = [];
if (data.values.length) {
  for (let v of data.values) {
    if(!v.length) {
      continue;
    }
    values.push(v.map(s => s.replace(/_/gm,' ')));
   
  }
 
}
else {
  values.push([]);
}
let combinations = {};
if (data.combinations.length) {
  data.combinations.forEach( (v,i) => combinations[i] = v);
}

export default Vue.extend({
  name: "app",
  data: () => ({
    predicate,
    subjects,
    values,
    combinations,
    enabledAdd: true,
    saveLabels,
    status: "idle",
    buttonLabel: saveLabels["idle"],
    valids: {}
  }),
  watch: {
    values(newValues) {
      for (let a of newValues) {
        if (a.includes("always")) {
          this.enabledAdd = false;
          return;
        }
      }
      this.enabledAdd = true;
    }
  },
  components: {
    AutoComplete
  },
  methods: {
    handleAnd(index) {
      this.combinations[index] = "and";
      this.values.push([]);
    },
    handleOr(index) {
      this.combinations[index] = "or";
      this.values.push([]);
    },
    handleRemove(index) {
      this.values.splice(index, 1);
      delete this.combinations[index];
    },
    handleValidityChange(index, value) {
      this.valids[index] = value;
    },
    isValid() {
      const values = Object.values(this.valids);
      return !values.includes(false) && values.length != 0;
    },
    save() {
    
      const t = this;
      t.status = "load";
      t.buttonLabel = t.saveLabels[t.status];
      ajaxCall(
        ajaxurl,
        "udesly_save_rule_content",
        nonce,
        "args",
        JSON.stringify({
          values: t.values,
          combinations: t.combinations,
          post_id
        })
      )
        .then(res => {
          if (res.ok) {
            t.status = "success";
            t.buttonLabel = t.saveLabels[t.status];
          } else {
            t.status = "fail";
            t.buttonLabel = t.saveLabels[t.status];
          }
        })
        .finally(() => {
          setTimeout(() => {
            t.status = "idle";
            t.buttonLabel = t.saveLabels[t.status];
          }, 2000);
        });
    }
  }
});
</script>

