export function flatUpStructure( predicate, subjects) {
    const obj = [];
    obj.push({
      id: "",
      options: ["always", ...Object.keys(subjects).map(value => predicate + " " + value + " ")]
    });
    obj.push({
        id: "always",
        options: ["."]
    })
    obj.push({
      id: predicate + " ",
      options: Object.keys(subjects).map(value => value + " ")
    });
  
    for (let v in subjects) {
      obj.push({
        id: [predicate, v].join(" ") + " ",
        options: Object.keys(subjects[v]).map(value => value),
        value: [sanitize_string(v)]
      });
      for (let j in subjects[v]) {
        obj.push({
          id: [predicate, v, j].join(" ") + "",
          options: subjects[v][j],
          value: [sanitize_string(v), sanitize_string(j)]
        });
      }
    }
    return obj.reverse();
  }
function sanitize_string(str)
  {
      str = str.replace(/^\s+|\s+$/g, ''); // trim
      str = str.toLowerCase();
  
      // remove accents, swap ñ for n, etc
      var from = "àáäâèéëêìíïîòóöôùúüûñç·/_,:;";
      var to   = "aaaaeeeeiiiioooouuuunc______";
  
      for (var i=0, l=from.length ; i<l ; i++)
      {
          str = str.replace(new RegExp(from.charAt(i), 'g'), to.charAt(i));
      }
  
      str = str.replace('.', '_') // replace a dot by a dash 
          .replace(/[^a-z0-9 -]/g, '') // remove invalid chars
          .replace(/\s+/g, '_') // collapse whitespace and replace by a dash
          .replace(/-+/g, '_'); // collapse dashes
  
      return str;
  }

  export function ajaxCall(url, action, nonce, dataName, data) {

    let formData = new FormData();
    formData.append('action', action);
    formData.append('security', nonce);
    formData.append(dataName, data);

return fetch(url, {
    method: 'POST',
    body: formData,
    credentials: 'same-origin'
});
}